import { getPlant } from '@/api/'
let actions = {
    sendPlantRequest({ commit }, data) {
        getPlant(data).then(resolve => {
            commit('setPlant', resolve.data)
        }, reject => {
            throw Error(reject.data.message)
        })
    }
}
let mutations = {
    setPlant(state, response) {
        let data = response.data
        state.pageData = data.result
        state.total = data.total
    }
}
let state = {
    pageData: [],
    total: NaN
}

export default {
    namespaced: true,
    actions,
    mutations,
    state
}