import axios from "axios";

console.log(process.env.VUE_APP_URL)
console.log(process.env.VUE_APP_TEST)
let requests_api = axios.create({
    baseURL: '/api/',
    timeout: 50000
})
// 请求plant data
export let getPlant = (data) => {
    return requests_api({ url: 'plants', method: 'get', params: data })
}
// get location data
export let getLocation = (data) => {
    return requests_api({ url: 'location', method: 'get', params: data })
}
// get comments data
export let get_comments = (data) => {
    return requests_api({ url: 'comments', method: 'get', params: data })
}
// get random comment
export let getRandomComments = () => {
    return requests_api({ url: 'randomComments', method: 'get' })
}