<template>
  <div class="table_data">
    <el-table
      :data="showData"
      border
      header-row-class-name="table_header"
      row-class-name="table-column"
      height="70%"
    >
      <el-table-column
        prop="name"
        label="名称"
        class-name="name_colnumn"
      ></el-table-column>
      <el-table-column prop="count" label="讨论次数"></el-table-column>
      <el-table-column
        prop="rank_reverse"
        label="排名"
        align="right"
        width="300"
      ></el-table-column>
    </el-table>
    <el-pagination
      class="pagination"
      background
      layout="prev, pager, next"
      :total="total"
      @current-change="pageNumberChange"
    >
    </el-pagination>
  </div>
</template>

<script>
export default {
  name: "tablepage",
  props: ["showData", "total"],
  methods: {
    pageNumberChange(page) {
      this.$emit("pageChange", page);
    },
  },
};
</script>

<style>
.table-column {
  width: 30%;
  font-weight: bold;
}
.table_data {
  padding: 40px;
  width: 100%;
  height: 100%;
  box-sizing: border-box;
}
.table_header {
  color: aqua;
  background: gainsboro;
}
.pagination {
  margin-top: 40px;
}
</style>