      ></el-table-column>
      <el-table-column label="分析" prop="level"></el-table-column>
    </el-table>
    <el-pagination
      page-size="50"
      class="pagination"
      background
      layout="prev, pager, next"
      :total="total"
      @current-change="pageChange"
