import { getLocation } from '@/api/index'

let actions = {
    sendLocationRequest({ commit }, data) {
        getLocation(data).then(resolve => { commit('setLocation', resolve.data) }, reject => {
            throw Error(reject.message)
        })
    }
}
let mutations = {
    setLocation(state, response) {
        let data = response.data
        state.pageData = data.result
        state.total = data.total
    }
}
let state = {
    pageData: [],
    total: NaN
}
export default {
    namespaced: true,
    actions,
    mutations,
    state
}