<template>
  <div ref="echar" class="echart"></div>
</template>

<script>
import * as echarts from "echarts/core";
import { PieChart } from "echarts/charts";
import {
  TitleComponent,
  TooltipComponent,
  GridComponent,
  DatasetComponent,
  TransformComponent,
  LegendComponent,
} from "echarts/components";
import { LabelLayout, UniversalTransition } from "echarts/features";
import { CanvasRenderer } from "echarts/renderers";

// 注册必须的组件
echarts.use([
  TitleComponent,
  TooltipComponent,
  GridComponent,
  DatasetComponent,
  TransformComponent,
  PieChart,
  LabelLayout,
  UniversalTransition,
  CanvasRenderer,
  LegendComponent,
]);
export default {
  name: "pieechart",
  props: ["pieData"],
  data() {
    return {
      echart: "",
      plantName: [],
      plantRank: [],
    };
  },
  watch: {
    pieData: function () {
      this.clearData();
      // 更新echart
      this.initChart();
    },
  },
  methods: {
    // 饼状图
    initChart() {
      this.echart = echarts.init(this.$refs.echar);
      this.echart.setOption({
        title: {
          text: "次数统计",
          left: "center",
        },
        tooltip: {
          trigger: "item",
          formatter: "{a} <br/>{b} : {c} ({d}%)",
        },
        legend: {
          type: "scroll",
          orient: "vertical",
          right: 10,
          top: 20,
          bottom: 20,
          data: this.plantNames,
        },
        series: [
          {
            name: "名称",
            type: "pie",
            radius: "55%",
            center: ["40%", "50%"],
            data: this.plantRank,
            emphasis: {
              itemStyle: {
                shadowBlur: 10,
                shadowOffsetX: 0,
                shadowColor: "rgba(0, 0, 0, 0.5)",
              },
            },
          },
        ],
      });
    },
    // 过滤数据
    clearData() {
      this.pieData &&
        this.pieData.forEach((element) => {
          let plant = element.name,
            value = element.count;
          this.plantName.push(plant);
          this.plantRank.push({ name: plant, value });
        });
    },
    // 页面大小变化
    pageSizeChange() {
      // 重新渲染echart
      this.echart.resize();
    },
  },
  mounted() {
    window.addEventListener("resize", this.pageSizeChange);
  },
  beforeDestroy() {
    // 注销pagesize改变世界
    window.removeEventListener("resize", this.pageSizeChange);
    this.echart.dispose();
  },
};
</script>

<style>
.echart {
  width: 500px;
  height: 500px;
}
</style>