import { get_comments, getRandomComments } from '@/api'
let actions = {
    async sendCommentRequest({ commit }, data) {
        return await get_comments(data).then(reslove => {
            commit('setComments', reslove.data)
        }, reject => {
            throw new Error(reject.data.reject)
        })
    },
    sendRandomComments({ commit }) {
        getRandomComments().then(resolve => {
            commit('setRandomComments', resolve.data)
        }, reject => {
            throw new Error(reject.data.message)
        }
        )
    }
}
let mutations = {
    setComments(state, comment) {
        let data = comment.data
        state.comments = data.comments
        state.total = data.total
    },
    setRandomComments(state, commnet) {
        state.randomComments = commnet.data
    }
}
let state = {
    comments: [],
    randomComments: [],
    total: NaN
}
export default {
    namespaced: true,
    actions,
    mutations,
    state
}