import VueRouter from "vue-router";
import Vue from "vue";
Vue.use(VueRouter)
let router = new VueRouter({
    mode: 'hash',
    routes: [{
        path: '/',
        component: () => import("@/page/index.vue")
    },
    {
        path: '/home',
        component: () => import('@/page/home.vue'),
        redirect: "/home/plantsWordCloud",
        children: [{
            name: 'plantWordCloud',
            path: 'plantsWordCloud',
            component: () => import("@/page/plants/plant.vue")
        }, {
            name: 'plantRank',
            path: 'plantRank',
            component: () => import("@/page/plants/rank.vue")
        },
        {
            name: 'location',
            path: "location",
            component: () => import("@/page/location/location.vue")
        },
        {
            name: 'locationRank',
            path: 'locationRank',
            component: () => import("@/page/location/locationRank.vue")
        }, {
            name: 'comments',
            path: 'comments',
            component: () => import("@/page/emoAnalysis/comment.vue")
        }, {
            name: 'commentsRank',
            path: 'commentsRank',
            component: () => import("@/page/emoAnalysis/commentRank.vue")
        }]
    }]
})
const { isNavigationFailure, NavigationFailureType } = VueRouter

function onComplate(resolve) {
}
function onAbort(reject) {
    // 处理重复导航
    if (isNavigationFailure(reject, NavigationFailureType.duplicated)) {
        return ''
    }
    throw new Error(reject)
}
let push = router.push
router.push = function (path) {
    console.log('调用了push', path)
    push.call(this, path, onComplate, onAbort)
}
export default router